.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

li{
  list-style: none;
  margin: 10px;
}
nav{
  display: flex;
  background-color: black;
  color: white;
}
footer{
  bottom: 0px;
  right: 0px;
  left: 0px; 
  /* height: 10px; */
  background-color: black;
  color: white;
  position: fixed;
  text-align: center;
  width: 100vw;
  justify-content: center !important;
}
.nav-fix{
  position: sticky;
  width: 100vw;
  top: 0;
}
